export default {
  data() {
    return {
      inputMsg: ''
    }
  },
  computed: {
    to() {
      return this.$route.params.teamId
    }
  },
  methods: {
    sendMsg() {
      if (/^\s*$/.test(this.inputMsg)) {
        this.$vux.alert.show({
          title: '请不要发送空消息'
        })
        return
      }
      this.$store.dispatch('sendMsg', {
        type: 'text',
        scene: 'team',
        to: this.to,
        text: this.inputMsg,
        needMsgReceipt: true
      })
      history.go(-1)
    }
  }
}



